html,
body,
#root {
  height: 100%;
  width: 100%;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: stretch;
      align-items: stretch;
  text-align: center;
  height: 100%;
  width: 100%;
}

.App-logo {
  display: block;
  height: auto;
  max-width: 100%;
  -webkit-animation: dropIn 1000ms;
          animation: dropIn 1000ms;
  margin-top: 50px;
  margin-bottom: 10px;
  opacity: 0.8;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: opacity 250ms ease-in-out, transform 250ms ease-in-out;
  -o-transition: opacity 250ms ease-in-out, transform 250ms ease-in-out;
  -webkit-transition: opacity 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  transition: opacity 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  transition: opacity 250ms ease-in-out, transform 250ms ease-in-out;
  transition: opacity 250ms ease-in-out, transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: rotate(0) scale(0.95);
  -ms-transform: rotate(0) scale(0.95);
      transform: rotate(0) scale(0.95);
  -webkit-backface-visibility: hidden;
}

.App-logo:hover {
  opacity: 1.0;
  -webkit-transform: rotate(2deg) scale(1.0);
  -ms-transform: rotate(2deg) scale(1.0);
      transform: rotate(2deg) scale(1.0);
