    -ms-flex: 1 1 100%;
        flex: 1 1 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }

  .App-intro {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 0 1;
        flex: 0 1;
    width: auto;
    padding: 20px;
