@-webkit-keyframes dropIn {
  0% {
    -webkit-transform: translate3d(0, -1000%, 0) scale(0.95);
            transform: translate3d(0, -1000%, 0) scale(0.95);
  }

  50% {
    -webkit-transform: translate3d(0, -1000%, 0) scale(0.95);
            transform: translate3d(0, -1000%, 0) scale(0.95);
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0) scale(0.95);
            transform: translate3d(0, 0, 0) scale(0.95);
  }
}

@keyframes dropIn {
  0% {
    -webkit-transform: translate3d(0, -1000%, 0) scale(0.95);
            transform: translate3d(0, -1000%, 0) scale(0.95);
  }

  50% {
    -webkit-transform: translate3d(0, -1000%, 0) scale(0.95);
            transform: translate3d(0, -1000%, 0) scale(0.95);
  }

  100% {
    -webkit-transform: translate3d(0, 0, 0) scale(0.95);
            transform: translate3d(0, 0, 0) scale(0.95);
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}


